/*===========================================*/
//fonts Beauty
@font-face {
  font-family: 'Beauty';
  src: url('../../fonts/Beauty/Beauty.woff2') format('woff2'),
  url('../../fonts/Beauty/Beauty.woff') format('woff'),
  url('../../fonts/Beauty/Beauty.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

//fonts CenturiGothic-Bold
@font-face {
  font-family: 'Century GothicBold';
  src: url('../../fonts/CenturyGothic-Bold/CenturyGothic-Bold.woff2') format('woff2'),
  url('../../fonts/CenturyGothic-Bold/CenturyGothic-Bold.woff') format('woff'),
  url('../../fonts/CenturyGothic-Bold/CenturyGothic-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

//fonts MinionPro-Regular
@font-face {
  font-family: MinionProRegular;
  src: url('../../fonts/MinionPro/MinionPro-Regular.woff2') format('woff2'),
  url('../../fonts/MinionPro/MinionPro-Regular.woff') format('woff'),
  url('../../fonts/MinionPro/MinionPro-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
//fonts MinionPro
@font-face {
  font-family: 'Minion Pro';
  src: url('../../fonts/MinionPro/MinionPro-It.woff2') format('woff2'),
  url('../../fonts/MinionPro/MinionPro-It.woff') format('woff'),
  url('../../fonts/MinionPro/MinionPro-It.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

//fonts TwCenMTStd
@font-face {
  font-family: 'TwCenClassMTStd-Regular';
  src: url('../../fonts/TwCenMTStd/TwCenClassMTStd-Regular.woff2') format('woff2'),
  url('../../fonts/TwCenMTStd/TwCenClassMTStd-Regular.woff') format('woff'),
  url('../../fonts/TwCenMTStd/TwCenClassMTStd-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: Minion_Pro_Regular, Book Antiqua, Palatino, Palatino Linotype, Palatino LT STD, Georgia, serif;
  font-size: 1.5em;
}

h1, h2, a, .profile .face .data {
  font-family: Atia, Book Antiqua, Palatino, Palatino Linotype, Palatino LT STD, Georgia, serif;
}

h1 {
  font-weight: 600;
  color: #020202;
  font-size: 24.5px;
  font-family: "Myriad Pro",sans-serif;
}

body > .main {
  z-index: 3;
  position: relative;
  width: 100%;
  background: repeating-linear-gradient(90deg, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05) 1px, transparent 1px, transparent 20%);
  background: -moz-repeating-linear-gradient(to right, rgba(0, 0, 0, .1), rgba(0, 0, 0, .1) 1px, transparent 2px, transparent 20%);
}

input[type=text], select {
  border: none;
  border-bottom: 3px solid;
  border-radius: 0;
  box-shadow: none;
}

textarea {
  border-radius: 0;
}

form .btn {
  border-radius: 0;
  -webkit-box-shadow: none;
  padding: 8px 40px;
}

a.contact {
  color: initial;
  text-decoration: underline;
}

/* IE11 hide native button (thanks Matt!) */
select::-ms-expand {
  display: none;
}

.selectdiv:after {
  content: '<>';
  font: 17px "Consolas", monospace;
  color: #333;
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  right: 14px;
  /*Adjust for position however you want*/

  top: 10px;
  padding: 0 0 2px;
  border-bottom: 1px solid #999;

  position: absolute;
  pointer-events: none;
}

.selectdiv select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.main > .content {
  padding: 0 0 0 0;
}

.header{
  position: relative;
}

.main > .content .header img {
  width: 100%;
}

.main .side_menu {
  position: absolute;
  display: inline-block;
  z-index: 3;
  height: 100%;
}

.back_rose {
  background-color: #FFBED6;
  height: 100%;
}

.rose {
  color: #FFBED6;
}

.main .side_menu > div {
  position: absolute;
  overflow: hidden;
}

.main .side_menu .closed {
  width: 361px;
  left: -361px;
  z-index: 3;
  transition: all 1s;
  padding-bottom: 30px;
}

.main .side_menu .closed .link {
  margin-left: 32px;
  text-decoration: underline;
  display: block;
  color: #A2BADE;
}

.main .side_menu .closed .link:hover {
  color: white;
}

.main .side_menu .opened {
  text-align: center;
}

.main .side_menu .opened .title {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  font-family: 'TwCenClassMTStd-Regular',sans-serif;
}

.main .side_menu .opened .ico {
  margin: 51px 29px 9px 29px;
  width: 25px;
}

.svg-1 {
  margin-bottom: 5%;
}

.svg-1 polyline,
.svg-1 line,
.svg-1 path {
  fill: none;
  stroke: #000;
  stroke-linecap: square;
  stroke-width: 3;
  transition: .5s;
}

.svg-1.menu path {
  d: path("M22,3 L3,22 M32,13 L13,32 M22,13 L13,22");
}

.svg-1.cross path {
  d: path("M26,9 L9,26 M9,9 L26,26 M50,-11 L46,-7");
}

/*.main .side_menu:hover .closed,*/
.main .side_menu .closed.active {
  left: 0;
}

.main .side_menu .closed .ico {
  margin-top: 42px;
  font-size: 48px;
  margin-left: 52px;
}

.main .side_menu .closed > ul {
  margin-left: 32px;
  -webkit-padding-start: 00px;
  list-style: none;
  font-size: 18px;
  margin-bottom: 730px;
  margin-top: 110px;
}

.main .side_menu ul {
  -moz-padding-start: 0;
}

.main .side_menu .closed ul li {
}

.main .side_menu .closed ul li a:after {
  content: " |";
  font-size: 12px;
  bottom: 2px;
  position: relative;
  margin-left: 3px;
  font-weight: bold;
}

.main .side_menu ul li a {
  color: #000;
}

.main .side_menu ul li a:hover {
  color: #fff;
  text-decoration: none;
}

.main .side_menu .button {
  color: initial;

  display: inline-block;
}

.main .side_menu .button.tog {
  position: absolute;
  top: 0;
  z-index: 4;
  left: 50%;
  transform: translateX(-50%);
}

.main .side_menu .button.menu {
  margin: 84px 20.5px 0;
}

.header {
  min-height: 100px;
}

.svg-1.cross path {
  d: path("M26,9 L9,26 M9,9 L26,26 M50,-11 L46,-7");
}

.header .user {
  position: absolute;
  top: 53px;
  right: 128px;
  text-align: center;
  z-index: 99999;
}

.header .user .choose_lang a.btn{
  font-size: 16px;
  color: #ffffff;
  border: 1px solid #ffffff;
  transition: all .3s;
  line-height: 31px;
  margin-top: 10px;
  border-radius: 0;
  padding: 0 15px;
  letter-spacing: 1px;
  font-family: 'Minion Pro Regular', sans-serif;

  &:hover{
    border-color: #ced3d5;
    color: #ced3d5;
  }
}

/*===========carousel============*/
.block-inner {
  display: inline-block;
  width: 300px;
}

.block-inner {
  padding: 2px 2px;
}

.block-inner img {
  width: 100%;
  margin-bottom: 10px;
}

.block-inner .name {
  margin-bottom: 15px;
}

.block-inner .about {
  height: 66px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.carousel_container {
  position: relative;
  overflow: initial;
  width: 100%;
  display: inline-block;
  padding-left: 97px;
}

.hometab {
  overflow: hidden;
  position: relative;
  width: 100%;
}

.navi {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  font-size: 48px;
  color: white;
  top: 30%;
  width: 30px;
  height: 50px;
  display: block;
  overflow: hidden;
  text-decoration: none;
  text-shadow: 1px 2px 4px #333333;
}

.prev {
  left: 117px;
}

.next {
  right: 10px;
}

.phone a.btn{
  color: #ffffff;
  font-family: 'Minion Pro Regular', sans-serif;
  transition: all .3s;
  font-size: 18px;
  letter-spacing: 1.8px;

  &:hover{
    color: #ced3d5;
  }
}

.navi:hover {
  color: #000;
  text-decoration: none;
}

footer {
  color: white;
}

/*===============modal================*/
.modal .close i {
  font-size: 38px;
  text-shadow: none;
}

.modal input[type=text], .modal input[type=email], .modal input[type=password] {
  background-color: initial;
  border: none;
  border-bottom: 1px solid;
  border-radius: 0;
  box-shadow: none;
  -webkit-box-shadow: none;
}

.catalog .profile_item > a {
  display: inline-block;
  width: 100%;
}

.catalog .profile_item .image img {
  width: 100%;
}

.catalog .profile_item .details p {
  height: 66px;
  overflow: hidden;
  text-overflow: ellipsis;
  /*white-space: nowrap;*/
  margin-bottom: 40px;
  margin-top: 15px;
}

.catalog .profile_item .control a {
  padding: 5px 35px;
  margin: 15px 5px;
  display: inline-block;
  background-color: white;
  color: #000;
}

.catalog .profile_item .control a:hover {
  text-decoration: none;
}

.profile .face .image img {
  padding: 0 40px;
}

.profile .face .data h1 {
  margin: 0;
}

.profile .face .data dl {
  display: table-row;
}

.profile .face .data dt, .profile .face .data dd {
  display: table-cell;
  padding: 10px 5px;
}

.profile .face .data dt {
  color: #bbb;
  font-weight: normal;
  text-transform: capitalize;
}

.profile .actions .button {
  padding: 5px 25px;
  margin: 15px 5px;
  display: inline-block;
  color: #000;
}

.profile .actions .button:hover {
  text-decoration: none;
  color: #000;
}

.profile .actions a {
  text-decoration: none;
}

.profile .actions .mr3 img {
  width: 64px;
}

.profile .carousel .slick-slide img {
  height: 256px;
}

.page {
  margin-top: 80px;
  margin-bottom: 60px;
  margin-left: /*-97*/0;
  padding: 0 39px;
  .box_wrapp{
    max-width: 915px;
