}

.con_img span {
  font-size: 36px;
  color: #747474;
  margin-left: 20px;
}

.con_img img {
  border-bottom: 23px solid #a3aab1;
  width: 100%;
}

.bg_cont p {
  width: 222px;
  font-size: 18px;
  color: #000000;
  letter-spacing: .7px;
  margin-top: 30px;
}

.bg_cont strong {
  color: #a3aab1;
